* {
  box-sizing: border-box;
}

p {
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2a2a2a;
  background-color: #a3c7d6;
  margin: 0;
  padding: 40px 50px;
  font-family: Roboto, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font-size: 1.1rem;
  font-weight: 600;
  line-height: 1;
  font: inherit;
  width: 100%;
  border: 0 solid #e2e8f0;
  border-radius: 3px;
  margin-top: 20px;
  margin-bottom: 16px;
  padding: 1rem 3.5rem;
  display: flex;
}

#search-box:focus {
  outline: 1px solid #ccc;
}

.country-info {
  grid-template-columns: 100%;
  gap: 20px;
  margin: 0 auto;
  display: grid;
}

.card {
  background: #ebf2fa;
  border-radius: 3px;
  flex-direction: column;
  margin: 0;
  padding: 20px;
  text-decoration: none;
  transition: box-shadow .2s linear;
  display: flex;
}

.card :nth-child(3) {
  order: 1;
  margin-bottom: 10px;
}

.card :nth-child(2) {
  order: 2;
}

.card > div > img {
  object-fit: contain;
  width: 100%;
  height: fit-content;
  align-self: center;
  margin-bottom: 5px;
}

.card > ul {
  flex-direction: row;
  justify-content: space-between;
  margin: 0 0 10px;
  padding-left: 10px;
  padding-right: 10px;
  display: flex;
}

.card > ul, .card > li {
  color: #2a2a2a;
  gap: 10px;
  list-style: none;
  display: flex;
}

.card p {
  color: #2a2a2a;
  text-shadow: 0 2px 3px rgba(255, 255, 255, .5);
  font-weight: 800;
}

.bottom-fr {
  color: #2a2a2a;
  margin: 0;
  padding-left: 0;
  text-decoration: none;
  list-style: none;
}

.bottom-fr > li {
  letter-spacing: .03rem;
  margin-bottom: 5px;
  padding-left: 10px;
  font-size: 18px;
}

.country {
  width: 100%;
  margin-bottom: 10px;
  font-size: 20px;
  font-weight: 600;
  display: inline-block;
}

#NotiflixNotifyWrap {
  position: absolute;
  top: 100px;
  left: 50%;
  transform: translateX(-50%);
}

@media (min-width: 600px) {
  #search-box {
    width: 390px;
  }

  .country-info {
    grid-template-columns: repeat(2, 1fr);
  }

  .card {
    grid-column-gap: 20px;
    grid-row-gap: 5px;
    grid-template-rows: auto;
    grid-template-columns: repeat(2, 1fr);
    display: grid;
  }

  .card :nth-child(3) {
    order: 2;
    margin-bottom: 0;
  }

  .card :nth-child(2) {
    order: 1;
  }

  .card > div > img {
    width: 240px;
    grid-area: 1 / 1 / 2 / 2;
  }

  .card > ul {
    flex-direction: column;
    grid-area: 1 / 2 / 2 / 3;
    justify-content: flex-start;
  }

  .bottom-fr {
    grid-area: 2 / 1 / 3 / 3;
  }

  .country {
    font-size: 26px;
  }

  #NotiflixNotifyWrap {
    position: absolute;
    top: 30px;
    left: 420px;
    transform: translateX(0);
  }
}

@media (min-width: 1200px) {
  .country-info {
    grid-template-columns: repeat(3, 1fr);
  }

  .card > div > img {
    width: 240px;
  }
}

.country-list {
  opacity: 0;
  width: 390px;
  background: #ebf2fa;
  border-radius: 3px;
  margin: 0;
  padding: 26px;
  list-style: none;
  transition: opacity .3s linear, transform .3s linear, box-shadow .3s linear;
  display: block;
  position: relative;
  top: -14px;
  transform: translateY(-100px);
}

.country-list.isVisible {
  opacity: 1;
  transform: translateY(0);
}

.country-list > li:not(:last-child) {
  margin-bottom: 10px;
}

.country-list > li {
  cursor: pointer;
  align-items: center;
  gap: 10px;
  font-size: 20px;
  transition: all .2s linear;
  display: flex;
}

.country-list > li:hover {
  border-radius: 3px;
  padding: 10px;
  scale: 1.05;
}

.country-list > li > img {
  object-fit: cover;
  height: 30px;
}

.lds-dual-ring {
  width: 24px;
  height: 24px;
  opacity: 0;
  transition: opacity .5s linear;
  display: inline-block;
  position: absolute;
  top: 44px;
  left: 33px;
}

.lds-dual-ring:after {
  content: " ";
  width: 24px;
  height: 24px;
  border: 2px solid #828282;
  border-color: #828282 rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: lds-dual-ring 1.2s linear infinite;
  display: block;
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

